*{ 
  box-sizing: border-box;
}
/* App Comp */
.App{
  width: 50vw;
  min-width: 300px;
  height: 50vh;
  background-color: #666666;
}

/* HabitTitle */
.habit__header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:0.3rem;
  background-color: #444444;
}

.habit__title{
  font-size: 1.5rem;
  color:#808080;
}

.header__count {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #afafaf;
  font-size: 1.5rem;
  font-weight: bold;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-color: #6f6f6f;
}

/* HabitInput */
.habit__input{
  background-color: transparent;
  margin: 0.4rem;
  padding:1rem;
  border:2px solid #555555;
  border-radius: 5px;
}

.input__additem{
  all:unset;
  height: 2rem;
  background-color: #555555;
  color:#6f6f6f;
  border-radius: 5px;
  padding:0.3rem;
  margin-right: 0.3rem;
}

.input__addbtn{
  all:unset;
  border-radius: 5px;
  font-size: 1rem;
  height: 2rem;
  padding:0.3rem;
  background-color: #555555;
  cursor: pointer;
  transition: all .2s ease;
  color:#6f6f6f;
}
.input__addbtn:hover{
  background-color: #606060;
  color:#555555;
}

/* Habits - habit */
.habit__list{
  all:unset;
  display: flex;
  flex-direction: column;
  background-color: #afafaf;
  min-height: 200px;
  overflow:hidden;
}

.habit__comp{
  all:unset;
  margin-bottom: 0.5rem;
  padding:0.3rem;
  display: flex;
  align-items: center;
}

.habit-button{
  all:unset;
  font-size: 2.1rem;
  color:#6f6f6f;
  margin-right: 0.1em;
}

.habit-name,
.habit-count{
  font-size: 1.5rem;
  font-weight: bold;
  margin-right: 0.3rem;
}

.habit-name {
  color:#4f4f4f;
}

.habit-count{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 40px;
  border-radius: 5px;
  color:#4f4f4f;
  background-color: #606060;
}

.habit-button:hover{
  color:#606060;
}

.habit-button:active{
  transform: scale(0.9);
}

/* HabitReset */

.habit__reset{
  background-color: #cfcfcf;
}

.reset__button{
  all:unset;
  cursor:pointer;
  font-size: 0.7rem;
  color:#333333;
  margin: 0.5rem;
}

.reset__button:hover{
  color:#555555;
}